import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, observable, BehaviorSubject} from 'rxjs';
import { map } from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
    'Ocp-Apim-Subscription-Key': '0a570f0bf03d46089b7829d7304831e4'
  })
};

@Injectable({
  providedIn: 'root'
})
export class FlightService {
  
  private resultsSource: BehaviorSubject<[]>;
  private dataStore: {
    flights: []
  };
  constructor(private http:HttpClient) {
    this.dataStore = {flights:[]};
    this.resultsSource = <BehaviorSubject<[]>>new BehaviorSubject([]);
  }

  searchFlights(param) { 
    const requestUrl = `https://api.qa.alaskaair.net/aag/1/dayoftravel/airports/${param}/flights/flightInfo?minutesBefore=10&minutesAfter=120`;
    console.log(requestUrl);

    // return this.http.get(requestUrl, httpOptions)
    //   .pipe(map((res:any)=> {
    //     this.resultsSource.next(res.response);
    //     return res.response;
    //   }))

    this.http.get(requestUrl, httpOptions).subscribe(data => {
      this.resultsSource.next(data['data']);
    });
  }

  getFlights() {
    return this.resultsSource.asObservable();
  }
}
